/**
 *@des 首页楼层组件
 *@author stav stavyan@qq.com
 *@blog https://stavtop.club
 *@date 2020/01/08 11:28:39
 */
export default {
    name: 'rfFloorIndex',
    props: {
        // 列表
        list: {
            type: Array,
            default() {
                return []
            }
        },
        // 广告图
        banner: {
            type: Object,
            default() {
                return {}
            }
        },
        // 标题 例: 新品上市
        header: {
            type: Object,
            default() {
                return {}
            }
        },
        // 是否显示banner图
        bannerShow: {
            type: Boolean,
            default: true
        },
    },
    data() {
        return {}
    },
    methods: {
        // 跳转详情页
        detail(id) {
        	if (!id) return;
            this.$emit('detail', {
                id
            });
        },
        // banner 跳转页
        toBanner(type, id) {
            this.$emit('toBanner', {
                type,
                id
            });
        },
        // 列表 跳转页
        toList() {
            this.$emit('toList');
        }
    }
}
